//
// BEM block for React PlayerPmrHeadshotComponent
// - PMR is full width, headshot is centered on top of it
// - percentage based
// - inception limit of nesting (max 3)
//
// NOTES:
// - if this gets below around 20px, the pmr stops showing because it's less than a pixel in width
//

$block: '.player-pmr-headshot';

#{$block} {
  position: relative;

  &__bg {
    background: #050814;
    border-radius: 50%;
    height: 94%;
    left: 3%;
    overflow: hidden;
    position: absolute;
    top: 3%;
    width: 94%;
  }

  &__headshot {
    display: block;
    position: relative;

    // sport specific headshot positioning
    &--mlb {
      height: 94%;
      left: 3%;
      top: 6%;
    }
    &--nba {
      left: -12%;
      top: 10%;
      width: 124%;
    }
    &--nfl {
      height: 100%;
      left: -9%;
      top: 0%;
    }

    // default player headshot
    &--default {
      opacity: 0.5;
      top: 20%;
      width: 100%;
    }
  }

  &__pmr {
    height: 100%;
    position: relative;
    width: 100%;
    z-index: 10;
  }


  // outer border for countdown
  &--upcoming {
    background: #45485d;
    border-radius: 50%;
  }

  // light background of player in results
  &--results #{$block}__bg {
    background: #ebedf2;
  }
}



